@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:wght@100;300&family=Nunito:ital,wght@0,200;0,500;1,400&family=Roboto+Flex:opsz,wght@8..144,200;8..144,400&display=swap");

.about {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100vw;
  height: 100vh;
}

.about .text {
  width: 70%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #f5f5f5;
  font-family: "Nunito", sans-serif;
}

.about h1 {
  font-size: 2rem;
  font-weight: bold;
  /* text-transform: uppercase; */
  /* margin-bottom: 1rem; */
}

.about p {
  font-size: 1.5rem;
  /* font-weight: bold; */
  /* text-transform: uppercase; */
  /* margin-bottom: 1rem; */
}

@media (max-width: 600px) {
  .about h1 {
    font-size: 10vw;
  }
  .about p {
    font-size: 5vw;
  }
}

@media (max-width: 470px) {
  .about h1 {
    font-size: 5vw;
  }
  .about p {
    font-size: 3vw;
  }
}
