footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  border-top: 2px solid #f5f5f5;
  color: #f5f5f5;
}

.logo {
  font-size: 1.25em;
}

@media (max-width: 580px) {
  footer {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

@media (max-width: 260px) {
  .logo {
    font-size: 5vw;
  }
  .footer-contact {
    font-size: 3vw;
  }
}
