.contact {
  width: 100%;
  /* height: 100vh; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #f5f5f5;
  text-align: center;
  /* padding: 9% 5%; */
}
.contact-info {
  height: 80vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  font-size: 1.25em;
}
.contact-info-text {
  font-weight: 500;
  /* margin-bottom: 1rem; */
}
@media (max-width: 260px) {
  .contact-info-text {
    font-size: 5vw;
  }
}
