@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:wght@100;300&family=Nunito:ital,wght@0,200;0,500;1,400&family=Roboto+Flex:opsz,wght@8..144,200;8..144,400&display=swap");

.products {
  display: flex;
  /* flex-wrap: wrap; */
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  max-width: 1200px;
  padding: 0 20px;
  height: 100vh;
  width: 100vw;
  flex-direction: row;
}

.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  /* max-width: 300px; */
  padding: 0 1em;
  width: 20%;
  height: 100%;
}

.product-img {
  width: 10vw;
  height: 10vw;
  background-color: #f5f5f5;
  border-radius: 50%;
  /* position: relative; */
  /* overflow: hidden; */
}

.product-img img {
  width: 50%;
  height: auto;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  /* border-radius: 50%; */
}

.product-text {
  font-size: 2em;
  font-weight: bold;
  color: #f5f5f5;
  text-align: center;
}

@media (max-width: 470px) {
  .product-text {
    font-size: 5vw;
  }
}

@media (max-width: 430px) {
  .product-text {
    font-size: 6vw;
  }
}

@media (max-width: 400px) {
  .products {
    flex-direction: column;
  }
  .product {
    width: 100%;
    margin: 0 auto;
    padding: 0;
  }
  .product-img {
    width: 25vw;
    height: 25vw;
  }
}
