@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:wght@100;300&family=Nunito:ital,wght@0,200;0,500;1,400&family=Roboto+Flex:opsz,wght@8..144,200;8..144,400&display=swap");

nav {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem 1rem;
  flex-direction: row;
  /* background: #fff; */
  /* border-bottom: 1px solid #e5e5e5; */
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.7);
  height: 10vh;
  min-height: 35px;
  max-height: 60px;
  transition: all 0.3s ease-in-out;
}

.logo a {
  color: #f5f5f5;
  font-size: 1em;
  font-weight: bold;
  transition: all 0.3s ease-in-out;
  text-transform: uppercase;
  cursor: pointer;
  font-family: "nunito", sans-serif;
  display: flex;
  align-items: center;
}
.logo img {
  width: 15%;
  object-fit: cover;
}

.nav-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.nav-menu ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
}

.nav-menu ul li {
  display: inline-block;
  margin: 0 1rem;
}

.nav-menu ul li a {
  color: #f5f5f5;
  font-size: 1rem;
  font-weight: bold;
  text-transform: uppercase;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}

.mobilenavbtn {
  display: none;
  width: 5vw;
  height: 3vw;
  position: relative;
  background-color: transparent;
  border: none;
  min-height: 15px;
}

.barmiddle {
  /* display: none; */
  width: 100%;
  height: 3px;
  background-color: #333;
}

.mobilenavbtn::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #333;
}
.mobilenavbtn::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #333;
}
.mobilenav {
  /* display: none; */
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #333;
  z-index: 9;
  opacity: 1;
  /* visibility: hidden; */
  /* display: flex; */
  /* display: none; */
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease-in-out;
}

.mobilenav ul {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  height: 60%;
  width: 100%;
  font-size: 1.5em;
}

.mobilenav-inner {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.mobilenavclose {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 5vw;
  height: 5vw;
  background-color: transparent;
  border: none;
}
.mobilenavclose:hover {
  cursor: pointer;
  background-color: blue;
}
.mobilenav ul li a {
  color: #f5f5f5;
  font-size: 1rem;
  font-weight: bold;
  text-transform: uppercase;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}
.mobilenavclose::after {
  content: "";
  position: absolute;
  top: 0;
  right: 44%;
  width: 15%;
  height: 100%;
  background-color: #f5f5f5;
  transform: rotate(45deg);
}
.mobilenavclose::before {
  content: "";
  position: absolute;
  top: 0;
  left: 44%;
  width: 15%;
  height: 100%;
  background-color: #f5f5f5;
  transform: rotate(-45deg);
}

@media (max-width: 992px) {
  .nav-menu ul {
    display: none;
  }
  .mobilenavbtn {
    display: block;
  }
}

@media (max-width: 390px) {
  .logo a {
    font-size: 4vw;
  }
}
