@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:wght@100;300&family=Nunito:ital,wght@0,200;0,500;1,400&family=Roboto+Flex:opsz,wght@8..144,200;8..144,400&display=swap");

.hero {
  height: 100vh;
  width: 100%;

  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  color: #fff;
  font-family: "Nunito", sans-serif;
}

.hero-text h1 {
  font-size: 3rem;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 1rem;
}
.hero-text p {
  font-size: 1.5rem;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 1rem;
}
.hero-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
}
.hero-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -2;
  display: none;
}
.hero-img .showing {
  display: block;
}

.hero-shader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: -1;
}

@media (max-width: 500px) {
  .hero-text h1 {
    font-size: 10vw;
  }
  .hero-text p {
    font-size: 5vw;
  }
}
